  visibility: none;

  animation: slide 3s alternate;

  transform-origin: center;
  transform-box: fill-box;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes slide {
  from {
    transform: translateX(-600px);
    visibility: visible;
  }
  to {
    transform: translateX(0px);
    visibility: visible;
  }
}

@keyframes drop {
  from {
    transform: translateY(500px);
    visibility: visible;
  }
  to {
    transform: translateY(0px);
    visibility: visible;
  }
}

@keyframes rise {
  from {
    transform: translateY(-500px);
    visibility: visible;
  }
  to {
    transform: translateY(0px);
    visibility: visible;
  }
}

#React {
  animation: rotate 50s linear infinite;
  transform-origin: center;
  transform-box: fill-box;
}
#BookLower {
  animation: bounce1 0.5s alternate infinite cubic-bezier(0.215, 0.61, 0.355, 1);
  transform-origin: center;
  transform-box: fill-box;
}
#BookUpper {
  animation: bounce2 0.5s alternate infinite cubic-bezier(0.215, 0.61, 0.355, 1);
  transform-origin: center;
  transform-box: fill-box;
}
#Pencil {
  animation: bounce3 0.5s alternate infinite cubic-bezier(0.215, 0.61, 0.355, 1);
  transform-origin: center;
  transform-box: fill-box;
}

@keyframes rotateLittle {
  from {
    transform: rotate(0deg);
    visibility: visible;
  }
  to {
    transform: rotate(20deg);
    visibility: visible;
  }
}

@keyframes bounce1 {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-80px);
  }
}

@keyframes bounce2 {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-100px);
  }
}
@keyframes bounce3 {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-120px);
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(3600deg);
  }
}
@media screen and (max-width: 600px) {
  .SvgAnimContainer {
    width: 100%;
    overflow: visible;
  }
}
